.prizeDiv{
    width: 100%;
    height: 100vh;
    background: linear-gradient(to bottom, #ffffff, #e0e1dd);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.prizeRow{
    width: 75%;
    height: 55vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.prizeColTwo{
    width: 30%;
    height: 85%;
    background-color: #575254;
    color: black;
    margin: 20px;
    border-radius: 10px;
    transition-duration: 0.6s; 
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    cursor: pointer;
    background-attachment: fixed;
    background-position: center;
}
.prizeColTwo:hover{
    height: 93%;
    transition-duration: 0.6s;
}
